import React from 'react'

const Card = ({ link, imgUrl, title, subTitle, extraClass, description, ...rest }) => {
    return (
        <li className={`border border-solid border-gray-800 card overflow-hidden relative w-full ${extraClass}`} style={{ height: '350px' }} >
            <a href={link} className="flex h-full w-full justify-start items-center" target="_blank" rel="noopener noreferrer">
                <img className="img w-full absolute top-0 left-0 object-cover mb-2" style={{ filter: 'brightness(40%)', height: '350px' }} src={imgUrl} alt="card" />
                <div className="p-8 text-left z-10">
                    <h1 className="text-white font-bold text-3xl">{title} - {subTitle}</h1>
                    <p className="text-white font-semibold text-sm">{description}</p>
                </div>
            </a>
        </li>
    )
}


